import $axios from 'axios';
import { LOGIN, LOGOUT } from './mutation-types';
const BASE_URL = 'http://localhost:7000'
const AUTH_URL = `${BASE_URL}/auth`


export async function logout({ commit }) {
    commit(LOGOUT.PENDING)
    try {
        commit(LOGOUT.SUCCESS)
        return true
    } catch (error) {
        commit(LOGOUT.FAILED)
        return false
    }
    // alert('HOLA')
}

export async function signIn({ commit }, credentials) {
    commit(LOGIN.PENDING)
    try {
        const { data } = await $axios.post(`${AUTH_URL}/signin`, credentials)
        commit(LOGIN.SUCCESS, data)
    } catch (error) {
        commit(LOGIN.FAILED)
        return error.response

    }
}

export async function signUp({ commit }, data) {

    try {
        const response = await $axios.post(`${AUTH_URL}/signup`, data)
        console.log(response);
        // return null
    } catch (error) {
        console.log(error.response);

        return error.response.data
    }
    // console.log('SIGN UP');
    // console.log(data);



}